
<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">德育评价</p>
            <p class="navBtn f24 cfff pl-30" @click="turnToHistory">历史</p>
        </div>
        <div class="posi">
            <div class="face">
                <img :src="data.headImage == '' ? img : $api.fileUrl + data.headImage" alt="" style="width:100%;height:100%;display:block;">
            </div>
            <!-- <div class="faceText f26">人脸识别</div> -->
            <div class="item-box">
                <div class="item-list ml-30">
                    <div class="item-title f30 c000 l">班级</div>
                    <div class="item-content f26 c666 r mr-30">{{data.classesName}}</div>
                </div>
                <div class="item-list ml-30">
                    <div class="item-title f30 c000 l">姓名</div>
                    <div class="item-content f26 c666 r mr-30">{{data.username}}</div>
                </div>
                <div class="item-list ml-30">
                    <div class="item-title f30 c000 l">学号</div>
                    <!-- <div class="item-content f26 c666 r mr-30">2018060606</div> -->
                    <input class="item-content f26 c666 r mr-30" type="number" v-model="input" v-on:input="getByStuNum">
                </div>
                <div class="item-list ml-30" @click="chooseEvent">
                    <div class="item-title f30 c000 l">事件</div>
                    <img class="more r mr-20" src="../../../assets/my/more.png" alt="">
                    <div class="item-content f26 c666 r mr-20">{{eventHtml.html}}</div>
                </div>
                <div class="item-list ml-30">
                    <div class="item-title f30 c000 l">选择分值</div>
                    <div class="item-content f26 c666 r mr-30">
                        <div class="mathChoose l f18" :class="{active : eventHtml.active == 1}" @click="activeFn(1)" v-if="eventHtml.minScore">{{eventHtml.minScore}}</div>
                        <div class="mathChoose l f18" :class="{active : eventHtml.active == 0}" @click="activeFn(0)" v-if="eventHtml.score">{{eventHtml.score}}</div>
                        <div class="mathChoose l f18" :class="{active : eventHtml.active == 2}" @click="activeFn(2)" v-if="eventHtml.maxScore">{{eventHtml.maxScore}}</div>
                    </div>
                </div>
                <div class="item-desc ml-30">
                    <div class="item-title clear f30 c000 mt-30">事件描述</div>
                    <div class="item-content f26 c666 mr-30">
                        <textarea name="" class="trexarea f26" id="" cols="30" rows="10" v-model="eventDesc" placeholder="请输入事件描述"></textarea>
                    </div>
                </div>
            </div>
            <div class="centerBtn f26 cfff text-center" @click="submitStudentsEval">录入</div>
        </div>
        <van-popup v-model="shows" position="right" :overlay="false" class="eventChoose">
            <div class="poup">
                <div>
                    <div class="loginNavFix">
                        <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returnPrev">
                        <p class="title f30 cfff text-center">事件选择</p>
                    </div>
                    <div class="content-posi">
                        <div class="tabChange mt-30">
                            <van-tabs v-model="active" type="card" color="#6b77ff">
                                <van-tab title="加分项">
                                    <div class="label l f22" :class="{active:item.selected}" v-for="(item,index) in label" :key="index" @click="clickThis(index)">{{item.itemName}}</div>
                                </van-tab>
                                <van-tab title="减分项">
                                    <div class="label l f22" :class="{active:item.selected}" v-for="(item,index) in labelDown" :key="index" @click="clickThisDown(index)">{{item.itemName}}</div>
                                </van-tab>
                            </van-tabs>
                        </div>

                    </div>
                    <div class="centerBtn f26 cfff text-center" @click="next">下一步</div>
                </div>
            </div>
        </van-popup>
    </div>
</template>

<script>
export default {
  name: 'HelloWorld',
    data () {
        return {
            input:'',
            img:require('../../../assets/index/face.png'),
            math:[{name:5,active:true},{name:10,active:false},{name:15,active:false}],
            show:true,
            shows:false,
            math:[{name:5,active:true},{name:10,active:false},{name:15,active:false}],
            showPoup:true,
            active:0,
            label:[],
            labelDown:[],
            eventHtml:{//选择事件
                type:'',
                html:'请选择（大类）',
                score:'',
                minScore:'',
                maxScore:'',
                active:0,
                id:''//评价项目id
            },
            data:{//用户信息
                username : '',
                headImage : '',
                classesName:'',
                id:''
            },
            checkedAdd:{},//加分
            checkedDown:{},//减分
            eventDesc:'',//事件描述
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/indexs'});//返回上一级
        },
        next(){
            var that = this;
            if(this.active == 0){
                if(this.checkedAdd == ''){
                    this.$toast('未选择加分项');
                }else{
                    this.eventHtml = {
                        type : 1,//1表示加分2表示减分
                        html : that.checkedAdd.itemName,
                        score:that.checkedAdd.score,
                        minScore:that.checkedAdd.minScore,
                        maxScore:that.checkedAdd.maxScore,
                        active:0,
                        id:that.checkedAdd.id
                    }
                }
            }else{
                if(this.checkedDown == ''){
                    this.$toast('未选择减分项');
                }else{
                    this.eventHtml = {
                        type : 2,//1表示加分2表示减分
                        html : that.checkedDown.itemName,
                        score:that.checkedDown.score,
                        minScore:that.checkedDown.minScore,
                        maxScore:that.checkedDown.maxScore,
                        active:0,
                        id:that.checkedDown.id
                    }
                }
            }
            this.shows = false;
            // this.$router.push({path:'/moraleducationdetail'});//返回上一级
        },
        activeFn(num){
            this.eventHtml.active = num;
        },
        chooseEvent(){
            this.shows = true;
        },
        turnToHistory(){
           this.$router.push({path:'/history'});//跳转进历史页面
        },
        clickThis(index){
            var label = this.label;
            for(var i in label){
                label[i].selected = false
            }
            label[index].selected = !label[index].selected;
            this.checkedAdd = label[index];
        },
        clickThisDown(index){
            var labelDown = this.labelDown;
            for(var i in labelDown){
                labelDown[i].selected = false
            }
            labelDown[index].selected = !labelDown[index].selected;
            this.checkedDown = labelDown[index];
        },
        returnPrev(){
            this.shows = false;
        },
        getByStuNum(){//监听input
            this.$http({url:this.$api.moraleducation.getByStuNum},{stuNum:this.input}).then(data => {
                console.log(data);
                if(data.code == 0){
                    if(data.data != null){
                        this.data = data.data;
                    }else{
                        this.data={
                            username : '',
                            headImage : '',
                            classesName:''
                        }
                    }
                    this.$toast.clear();
                }else{
                    this.$toast(data,msg);
                }
            },err => {
                this.$toast('出错了');
            })
        },
        getItemList(){//加减分列表
            this.$http({url:this.$api.moraleducation.getItemList},{schoolId:JSON.parse(localStorage.getItem('zhxy_user')).schoolId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    for(var i in data.data){
                        data.data[i].selected = false;
                        if(data.data[i].itemType == 1){
                            this.label.push(data.data[i])
                        }else{
                            this.labelDown.push(data.data[i])
                        }
                    }
                    this.$toast.clear();
                }else{
                    this.$toast(data,msg);
                }
            },err => {
                this.$toast('出错了');
            })
        },
        submitStudentsEval(){//录入
            if(this.data.username == ''){
                this.$toast('请确认学生信息是否正确');
                return
            }
            if(this.eventHtml.type == ''){
                this.$toast('请选择加减分项');
                return
            }
            if(this.eventDesc == ''){
                this.$toast('请输入事件描述');
                return
            }
            var crmEvalStudent = {
                type:1,//（1：老师，2：学生）
                studentId:this.data.id,//学生id
                classesId:this.data.classesId,//班级id
                evalItemId:this.eventHtml.id,
                scoreType:this.eventHtml.type,//1:加分，2：减分
                evalDesc:this.eventDesc,//	事件描述
                evalItemName:this.eventHtml.html,//评价项的名称
                score:'',
                submitType:1,//1老师2学生
                submitId:JSON.parse(localStorage.getItem('zhxy_user')).id//	提交人id
            };
            if(this.eventHtml.active == 0){
                crmEvalStudent.score = this.eventHtml.score;
            }else if(this.eventHtml.active == 1){
                crmEvalStudent.score = this.eventHtml.minScore;
            }else{
                crmEvalStudent.score = this.eventHtml.maxScore;
            }
            this.$http({url:this.$api.moraleducation.submitStudentsEval},crmEvalStudent).then(data => {
                console.log(data);
                if(data.code == 0){
                    // for(var i in data.data){
                    //     data.data[i].selected = false;
                    //     if(data.data[i].itemType == 1){
                    //         this.label.push(data.data[i])
                    //     }else{
                    //         this.labelDown.push(data.data[i])
                    //     }
                    // }
                    // this.$toast.clear();
                }else{
                    this.$toast(data,msg);
                }
            },err => {
                this.$toast('出错了');
            })
        }

    },
    created() {
        this.getItemList();
    },
}
</script>

<style scoped>
    .posi{position: absolute;top: 0.84rem;right: 0;bottom: 0;left: 0;overflow-y: auto;}
    .face{width: 4rem;height: 2.5rem;background-size:100% 100%;margin:1.14rem auto 0;}
    .faceText{padding: 0.2rem 0;text-align: center;border-bottom: 1px solid #f2f2f2;}
    .item-box{width: 100%;box-sizing: border-box}
    .item-list{border-bottom: 1px solid #f2f2f2;overflow: hidden;line-height: 1rem;}
    .more{width: 0.3rem;height: 0.3rem;display: block;margin-top: 0.35rem;}
    .mathChoose{width: 0.8rem;height: 0.4rem;background: #f3f3f3;border-radius: 7px;text-align: center;line-height: 0.4rem;margin-top: 0.3rem;margin-left: 0.15rem;color: #c0c0c0;}
    .mathChoose.active{color: #d1d4f8;background: #7c88ee;}
    .item-desc{width:cale(100% - 0.3rem);overflow: hidden;}
    .trexarea{margin:0 0.3rem 0 0;padding: 0.3rem 0;width: 100%;outline: 0;border: 0;color: #b6b6b6;height: 2rem;margin-bottom: 2rem;resize: none;}
    .clear{clear: both !important;}
    .centerBtn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;position: fixed;bottom: 0.6rem;left: 50%;margin-left: -3.45rem;}
    .poup{width: 100vw;height: 100vh;}
    

     .content-posi{position: absolute;top: 0.84rem;left: 0;bottom: 2rem;right: 0;}
    .label{color: #b2b2b2;text-align: center;padding: 0.2rem 0.28rem;background: #f3f3f3;border-radius: 0.07rem;margin: 0.15rem 0.15rem}
    .label.active{background: #6b77ff;color: #fff;} 
    .centerBtn{height: 0.9rem;width: 5rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;position: fixed;bottom: 0.6rem ;left: 50%;margin-left: -2.5rem;}
    input.item-content{border: none;height: 1rem;;display: block;padding: 0 0.1rem;line-height: 1rem;text-align: right;}
</style>





